@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Roboto:wght@100;300;400;500;700&display=swap');
@import "normalize.css";

@mixin flex($parameter1,$parameter2,$parameter3,$parameter4) {
  display: $parameter1;
  flex-direction: $parameter2;
  justify-content: $parameter3;
  align-items: $parameter4;
}

@mixin wider-margin {
  margin: 0 100px;
}

@mixin align-right {
  justify-content: flex-end;
}

@mixin align-left {
  justify-content: flex-start;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffff;
  font-family: 'Roboto', sans-serif;
}

.container {
  &__align {
    @include flex(flex, row, space-between, center);
    height: 48.6px;
    margin: 0 100px;

    &--footer {
      justify-content: center;
    }
  }

  &--left {
    width: 50%;
    padding: 0px 15px;
    @include flex(flex, row, space-between, center);
    justify-content: flex-start;
  }

  &--right {
    width: 50%;
    @include flex(flex, row, space-between, center);
    justify-content: flex-end;
    padding: 0px 15px;
  }

  &--right a {
    @include flex(flex, row, space-between, center);
  }
}
img {
  display: block;
  width: 100%;
  height: auto;
}
.info {
  display: inline-block;
  height: 100%;
  text-align: center;
  font-size: 11px;
  padding: 0px 15px 0px 0px;
  text-transform: uppercase;
  font-weight: 300;
}

.header {
  &__top {
    background-color: #fef7f6;
    height: 48.6px;
    font-size: 11px;
    width: 100%;
  }

  &__lower {
    font-size: 11px;
    width: 100%;
  }
}

.navigation-bar {
  height: 90.4px;
  @include wider-margin;
  @include flex(flex, row, space-between, center);

  &__item {
    @include flex(flex, row, space-between, center);

    &--element {
      position: relative;
      display: flex;
      align-items: flex-start;
      justify-content: center;

      :hover.icons-header {
        color: #ff5353;
      }
    }
  }
}

.cloud {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 13px;
  width: 13px;
  border-radius: 50%;
  background-color: #ff5353;
  color: #fef7f6;
  font-size: 8px;
}

.icons-header {
  text-decoration: none;
  @include align-right;
  display: flex;
  align-items: flex-start;
}
.ti-angle-down {
  position: relative;
  color: black;
  left: 3px;
}

.sky {
  position: absolute;
  top: 0;
  right: 0;
}

.logo {
  position: relative;
  display: inline;
}

.menu {
  list-style: none;
  position: relative;
  display: flex;
  flex-direction: row;

  &__item {
    display: block;
    color: black;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    padding: 0 15px;
    font-size: 13px;
    text-transform: uppercase;
  }

  :hover.menu__item {
    color: #ff5353;
  }
}

.dropbtn {
  padding: 35px 15px;
  border: none;
  cursor: pointer;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  left: 0;
  background-color: white;
  min-width: 220px;
  box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 7%);
  z-index: 1;
  list-style: none;
  transition: all 300ms ease;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.dropdown-content a {
  color: black;
  padding: 10px 10px;
  text-decoration: none;
  display: block;

}

.dropdown-content a:hover {
  background-color: #f1f1f1;
  color: #ff5353;
}

.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown:hover .dropbtn {
  color: #ff5353;
}
.banner {
  display: block;
  width: 100%;
  position: relative;

  &__fluid {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
  }

    &__order {
      width: 100%;
      padding-right: -15px;
      padding-left: -15px;
      margin-right: auto;
      margin-left: auto;
      flex-wrap: wrap;
      @include flex(flex, row, space-between, center);
    &--column {
      position: relative;
      width: 50%;
      height: 100%;
      padding: 0 15px;
      @include flex(flex, row, space-between, center);
    }
    }

  &__substance {
    position: relative;
    width: 100%;
    @include flex(flex, column, space-between, center);
    @include align-right;
  }

  &__image {
    position: relative;
    width: 100%;
    height: auto;
    @include align-left;
  }
}
.first-banner {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  background-image: url("https://demo.themeies.com/silon/images/bg-1.jpg");
}
.second-banner {
  background-color: #e9e2d8;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.content__button:hover.content__button--content {
  color: blue;
  background-color: white;
}

.content {
  position: relative;
  text-align: center;
  padding: 15px 15px;
  @include flex(flex, column, space-between, center);
  flex-wrap: nowrap;

  &__button {
    background: #ff5353;
    width: 170px;
    height: 51.2px;
    border-radius: 8px;

    &--content {
      display: inline-block;
      text-transform: uppercase;
      color: #ffff;
      padding: 20px 25px;
      text-align: center;
      text-decoration: none;
      margin-left: auto;
      margin-right: auto;
      font-size: 14px;
      font-weight: 500;
    }
  }
}

.first-banner__subtitle {
  height: 11.2px;
  text-transform: uppercase;
  color: #565656;
  margin: 0;
}

