.product:hover {
  .product__item--hover {
    visibility: visible;
    position: relative;
    top: -200px;
    transition: top 300ms ease-in-out;
  }
}
.product {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;

  &__item {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    box-sizing: border-box;

    &--hover {
      top: -180px;
      position: relative;
      visibility: hidden;
      display: flex;
      text-decoration: none;
      justify-content: center;
    }

    &--details {
      :hover.item__name {
        color: #ff5353;
      }
      p.item__name {
        font-size: 14px;
        color: #888;
        margin-top: 25px;
      }
    }
  }
}
